@mixin pc {
    @media (max-width: 992px) {
        @content
    }
}

@mixin pad {
    @media (max-width: 767px) {
        @content
    }
}

@mixin phone {
    @media (max-width: 576px) {
        @content
    }
}

@mixin phone-sm {
    @media (max-width: 375px) {
        @content
    }
}